import React from 'react'
import recipesService from '../../services/recipes'

function EditRecipe({user, createRecipeHandler, editable, setEditable, match, cat, group, addArrays, setAddArrays}) {
  if(!user) return <></>

  if(!editable.title && match.params.id) {
    recipesService
    .getOne(match.params.id, '')
    .then(recipe => {
      setEditable({...recipe})
      const arr = []
      recipe.group.map(group => 
        arr.push(group)
      )
      const obj = addArrays
      obj.group = arr
      setAddArrays({...obj})
      console.log(addArrays)
      return <></>
    })
  }
  
  const handleChange = (e) => {
    const edit = editable
    edit[e.target.name] = e.target.value
    setEditable({...edit})
  }

  const handleGroup = (e) => {
    const arr = addArrays
    const index = arr.group.indexOf(e.target.value)
    
    if (index >= 0) {
      arr.group.splice(index, 1)
      setAddArrays({...arr})
      return
    }

    arr.group.push(e.target.value)
    setAddArrays({...arr})
  }

  const Groups = ({addArrays}) => {
    return (
      <ul id="search">
        {group.map(opt => {
          if(opt === 'Kaikki') return null
          const index = addArrays.group.indexOf(opt.toLowerCase())
          if(index >= 0) {
            return <li key={opt}><input type="checkbox" name={addArrays[index]} onChange={handleGroup} value={opt.toLowerCase()} checked />{opt}</li>
          }

          return <li key={opt}><label><input type="checkbox" onChange={handleGroup} value={opt.toLowerCase()} />{opt}</label></li>
        })}
      </ul>
    )
  }


  return (
    <div id="addRecipe">
      <div className="formWrap">
        <div className="form">
          <h3>Lisää uusi resepti</h3>
          <form onSubmit={createRecipeHandler}>
            <select name="category" value={editable.category || ''} onChange={handleChange} >
              {cat.map((cat, i) => {
                if(cat === 'Kaikki') return null
                return <option key={i} value={cat.toLowerCase()}>{cat}</option>
              })}
            </select>
            <Groups addArrays={addArrays}/>
            <input name="title" value={editable.title || ''} onChange={handleChange} placeholder="Otsikko" required />
            <textarea name="description" value={editable.description || ''} onChange={handleChange} placeholder="Kuvaus" rows="5" required />
            <textarea name="ingredients" value={editable.ingredients || ''} onChange={handleChange} placeholder="Ainekset (erota pilkulla)" rows="5" required />
            <textarea name="steps" value={editable.steps || ''} onChange={handleChange} placeholder="Vaiheet (erota pilkulla)" rows="5" required />
            <input name="servings" type="number" value={editable.servings || ''} onChange={handleChange} placeholder="Annokset" />
            <input name="timeToMake" type="number" value={editable.timeToMake || ''} onChange={handleChange} placeholder="Valmistus aika" />
            <input type="hidden" name="editId" value={match.params.id || ''} />
            <button>Lisää</button>
          </form>
        </div>
      </div> 
    </div>
  )
}

export default EditRecipe