import React, { useState, useEffect } from 'react'
import { BrowserRouter as Router, Route, Link } from "react-router-dom"
import {Redirect} from 'react-router'
import anime from 'animejs'

import './App.scss'

import recipesService from './services/recipes'
import userService from './services/user'
import loginService from './services/login'
import loginSessions from './services/loginSessions'

import Navigation from './components/model/Navigation'
import Login from './components/model/Login'
import Register from './components/model/Register'
import Recipes from './components/model/Recipes'
import MyRecipes from './components/model/MyRecipes'
import Recipe from './components/model/Recipe'
import User from './components/model/User'
import AddRecipe from './components/model/AddRecipe'
import EditRecipe from './components/model/EditRecipe'
import HeroRecipe from './components/model/HeroRecipe'


function App() {
  const [recipes, setRecipes] = useState()
  const [heroRecipe, setHeroRecipe] = useState()
  const [recipe, setRecipe] = useState()
  const [user, setUser] = useState()
  const [userRecipes, setUserRecipes] = useState()
  const [dest, setDest] = useState()
  const [font, setFont] = useState("'Bangers', cursive")
  const [redirect, setRedirect] = useState()
  const [editable, setEditable] = useState({})
  const [filter, setFilter] = useState(['rand','kaikki'])
  const [addArrays, setAddArrays] = useState({group:[], ingredients:[], steps:[]})

  const cat = [
    'Kaikki',
    'Aamupala',
    'Lounas',
    'Päivällinen',
    'Välipala',
    'Iltapala',
    'Jälkiruoka'
  ]

  const group = [
    'Kana ja linnut',
    'Punainenliha',
    'Kasvis',
    'Kala ja äyriäiset',
    'Maidoton',
    'Kananmunaton',
    'Marjat ja hedelmät',
    'Vilja ja riisi',
    'Maitotuotteet',
    'Kananmuna'
  ]

  const fonts = [
    "'Bangers', cursive",
    "'Creepster', cursive",
    "'Ewert', cursive",
    "'Fascinate', cursive",
    "'Metal Mania', cursive",
    "'Oleo Script Swash Caps', cursive",
    "'Pacifico', cursive",
    "'Patua One', cursive",
    "'Permanent Marker', cursive",
    "'Piedra', cursive",
    "'Ranchers', cursive",
    "'Trade Winds', cursive"
  ]

  const handleLoad = () => {
    let opacity = 1;
    const loader = document.getElementById('loader')

    const refreshIntervalId = setInterval(() => {
      if(opacity <= 0) {
        clearInterval(refreshIntervalId)
        loader.remove()
      }
      opacity -= .05
      loader.style.opacity = opacity
    }, 10)
  }
  
  useEffect(() => {
    window.addEventListener('load', handleLoad)
    const user = loginSessions.checkLogin()
    if(user) {
      setUser(user)
      recipesService
      .getAllById(user.id)
      .then(recipes => {
        setUserRecipes(recipes)
      })
    }

    recipesService
    .getAll()
    .then(recipes => {
      setRecipes(recipes)
      const rand = Math.floor(Math.random() * (recipes.length))
      setHeroRecipe(recipes[rand])
    })
  }, [])
  
  const handleChange = (e) => {
    if (e) {
      const name = e.target.getAttribute('name')
      const tar = e.target.value

      recipesService
      .getAll()
      .then(recipes => {
        setRecipes(recipes)

        let allRecipes = recipes
        if (filter[0] === 'ownRand' && userRecipes.length > 0 && user && name !== 'recipesCategory' && name !== 'myRecipesCategory') allRecipes = userRecipes
        
        if (name === 'recipesCategory' || name === 'myRecipesCategory') {
          const filt = filter
          filt[1] = tar === 'kaikki' ? '' : tar
          setFilter(filt)
        }

        allRecipes = allRecipes.filter(recipe => {
          if(!filter[1] ||filter[1] === 'kaikki') return recipe
          
          if(recipe.category === filter[1]) return recipe
          return null
        })

        if (name === 'recipesCategory') {
          setRecipes(allRecipes)
          return
        }

        if (name === 'myRecipesCategory') {
          allRecipes = allRecipes.filter(recipe => recipe.userID === user.id)
          const arr = addArrays.group.map(val => {
            return allRecipes.filter(recipe => recipe.group.indexOf(val))
          })

          console.log(arr)

          setUserRecipes(allRecipes)
          return
        }

        const rand = Math.floor(Math.random() * (allRecipes.length))
        setFont(fonts[Math.floor(Math.random() * fonts.length)])
        setHeroRecipe(allRecipes[rand])
      })   
    } else {
      recipesService
      .getAllByGroups(addArrays.group, user.id)
      .then(recipes => {
        setUserRecipes(recipes)
      })
      recipesService
      .getAllByGroups(addArrays.group)
      .then(recipes => {
        setRecipes(recipes)
      })
    }
  }

  const handleOpen = (e) => {
    e.preventDefault()

    const vals = e.target.value.split(':')
    recipesService
    .getOne(vals[0])
    .then(recipe => {
      setRecipe(recipe)
      setDest(vals[1])
      setRedirect()
      setRedirect(`/recipe/${recipe.title.replace(' ', '_')}`)
    })
  }

  const goTo = (e) => {
    e.preventDefault()

    setRedirect()
    setRedirect(e.target.value)
  }

  const loginHandler = (e) => {
    e.preventDefault()

    loginService
    .login(e.target[0].value, e.target[1].value)
    .then(user => {
      recipesService
      .getAllById(user.id)
      .then(recipes => {
        setUser(user)
        setUserRecipes(recipes)
        loginSessions.saveLogin(user)
        setRedirect()
        setRedirect('/')
      })
    })
  }

  const registerHandler = (e) => {
    e.preventDefault()

    const obj = {
      username: e.target[0].value,
      email: e.target[1].value,
      password: e.target[2].value,
    }
    userService
    .create(obj)
    .then(user => {
      setUser(user)
      loginSessions.saveLogin(user)
      setRedirect()
      setRedirect('/')
    })
  }

  const createRecipeHandler = (e) => {
    e.preventDefault()

    const obj = {
      category: e.target.category.value,
      group: addArrays.group,
      title: e.target.title.value,
      description: e.target.description.value,
      ingredients: e.target.ingredients.value.includes(',') ? e.target.ingredients.value.split(',') : e.target.ingredients.value,
      steps: e.target.steps.value.includes(',') ? e.target.steps.value.split(','): e.target.steps.value,
      servings: e.target.servings.value,
      timeToMake: e.target.timeToMake.value,
      userID: user.id,
    }

    if(e.target.editId.value) {
      delete obj.userID

      recipesService
      .edit(e.target.editId.value, user.id, user.loginKEY, obj)
      .then(res => {
        setRecipe(res)
        recipesService
        .getAll()
        .then(recipes => {
          setRecipes(recipes)
        })
        recipesService
        .getAllById(user.id)
        .then(recipes => {
          setUserRecipes(recipes)
        })
        setRedirect()
        setRedirect(`/recipe/${res.title.replace(' ', '_')}`)
      })
    } else {
      recipesService
      .create(user.loginKEY, obj)
      .then(res => {
        console.log(res)
        setRecipe(res)
        recipesService
        .getAll()
        .then(recipes => {
          setRecipes(recipes)
        })
        recipesService
        .getAllById(user.id)
        .then(recipes => {
          setUserRecipes(recipes)
        })
        setDest('myRecipes')
        setRedirect()
        setRedirect(`/recipe/${res.title.replace(' ', '_')}`)
      })
    }
  }

  const handleLogout = (e) => {
    e.preventDefault()
    loginSessions.clearLogin()
    
    loginService
    .logout(user.id, user.loginKEY)
    .then(res => {
      setUser('')
      setUserRecipes('')
      setRedirect()
      setRedirect('/')
    })
  }

  const handleUserChange = (e) => {
    e.preventDefault()

    const obj = {
      username: e.target.username.value,
      email: e.target.email.value,
      password: e.target.newPass.value,
    }

    userService
    .edit(user.username, e.target.oldPass.value, user.loginKEY, obj)
    .then(res => {
      console.log(res)
      setUser(res)
    })
  }

  const delUser = (e) => {
    e.preventDefault()

    userService
    .del(user.username, window.prompt("Oletko varma että haluat poistaa käyttäjätilisi? Anna salasana ja paina ok."), user.loginKEY)
    .then(() => {
      setUser()
      setRedirect()
      setRedirect('/')
    })
  }

  const delRecipe = (e) => {
    e.preventDefault()

    if(window.confirm("Oletko varma että haluat poistaa tämän reseptin?")) {
      recipesService
      .del(e.target.value, user.id, user.loginKEY)
      .then(() => {
        setUser(user)
        recipesService
        .getAllById(user.id)
        .then(recipes => {
          setUserRecipes(recipes)
          setRedirect()
          setRedirect('/myRecipes')
      })
      })
    } else {
      return
    }

  }

  const changeFilter = (e) => {
    e.preventDefault()
    const filt = filter

    switch (e.target.value) {
      case 'ownRand':
          filt[0] = e.target.value
          setFilter(filt)
        break;
      case 'rand':
          filt[0] = e.target.value
          setFilter(filt)
        break;
      default:
        if(e.target.getAttribute('name') === 'category') {
          if(e.target.value === 'kaikki') {
            filt[1] = ''
            setFilter(filt)
            break;
          }
          filt[1] = e.target.value
          setFilter(filt)
        }
        break;
    }
    handleChange(e)
  }
  
  const hamburgerClick = (e) => {
    e.preventDefault()

    const nav = document.getElementById('nav')
    const burger = document.getElementById('hamburger')

    if(burger.classList.contains('active')) {
      nav.style.display = 'none'
      burger.classList.remove('active')
    } else {
      nav.style.display = 'flex'
      burger.classList.add('active')
    }
  }

  const clearEdit = () => {
    setEditable({})
  }

  return (
    <Router>
      <div id="loader"><h1>Ladataan</h1></div>
      {redirect ? <Redirect to={redirect} /> : false}
      <div className="App">
        <div className="bg"></div>
        <button id="hamburger" onClick={hamburgerClick}><i className="fas fa-bars" /></button>
        <Navigation Link={Link} user={user} handleLogout={handleLogout} clearEdit={clearEdit} />
        
        <Route exact path='/'
          render={(routeProps) => (
            <HeroRecipe
            {...routeProps}
            {...{
              font: font,
              recipe: heroRecipe,
              handleOpen: handleOpen,
              handleChange: handleChange,
              cat: cat,
              changeFilter: changeFilter,
              user: user
            }} />
          )}
        />

        <Route path='/login'
          render={(routeProps) => (
            <Login
            {...routeProps}
            {...{
              loginHandler: loginHandler
            }} />
          )}
        />

        <Route path='/register'
          render={(routeProps) => (
            <Register
            {...routeProps}
            {...{
              registerHandler: registerHandler
            }} />
          )}
        />

        <Route path='/user'
          render={(routeProps) => (
            <User
            {...routeProps}
            {...{
              user: user,
              handleUserChange: handleUserChange,
              delUser: delUser
            }} />
          )}
        />

        <Route path='/recipe/:id'
          render={(routeProps) => (
            <Recipe
            {...routeProps}
            {...{
              recipe: recipe,
              goTo: goTo,
              dest: dest,
              user: user,
              delRecipe: delRecipe,
              setRecipe: setRecipe,
              Link: Link
            }} />
          )}
        />

        <Route path='/recipes'
          render={(routeProps) => (
            <Recipes
            {...routeProps}
            {...{
              recipes: recipes,
              handleOpen: handleOpen,
              cat: cat,
              handleChange: handleChange,
              group: group,
              addArrays: addArrays,
              setAddArrays:setAddArrays
            }} />
          )}
        />

        <Route path='/myRecipes'
          render={(routeProps) => (
            <MyRecipes
            {...routeProps}
            {...{
              recipes: userRecipes,
              handleOpen: handleOpen,
              setEditable: setEditable,
              cat: cat,
              handleChange: handleChange,
              group: group,
              addArrays: addArrays,
              setAddArrays:setAddArrays
            }} />
          )}
        />

        <Route path='/addRecipe/'
          render={(routeProps) => (
            <AddRecipe
            {...routeProps}
            {...{
              user: user,
              createRecipeHandler: createRecipeHandler,
              editable, setEditable,
              cat: cat,
              group: group,
              addArrays: addArrays,
              setAddArrays:setAddArrays
            }} />
          )}
        />

        <Route path='/editRecipe/:id'
          render={(routeProps) => (
            <EditRecipe
            {...routeProps}
            {...{
              user: user,
              createRecipeHandler: createRecipeHandler,
              editable, setEditable,
              cat: cat,
              group: group,
              addArrays: addArrays,
              setAddArrays:setAddArrays
            }} />
          )}
        />

      </div>
    </Router>
  )
}

export default App
