import React from 'react'

function MyRecipes({recipes, handleOpen, setEditable, cat, handleChange, group, addArrays, setAddArrays}) {
  setEditable('')
  const Recipe = ({recipe}) => {
    return (
      <div>
        <h3>{recipe.title}</h3>
        <p>{recipe.description}</p>
        <button onClick={handleOpen} value={`${recipe.id}:myRecipes`} >Avaa</button>
      </div>
    )
  }

  const handleGroup = (e) => {
    const arr = addArrays
    const index = arr.group.indexOf(e.target.value)

    if (index >= 0) {
      arr.group.splice(index, 1)
      setAddArrays({...arr})
      handleChange()
      return
    }

    arr.group.push(e.target.value)
    setAddArrays({...arr})

    handleChange()
  }

  // Double wrappers to hide scroll bar
  return (
    <div id="myRecipes">
      <div className="wrapper--wrapper">
        <div className="wrapper">
          <div className="recipe__Header">
            <h1>Minun reseptit:</h1>
              <select name="myRecipesCategory" onChange={handleChange}>
                {cat.map((cat, i) => <option key={i} value={cat.toLowerCase()}>{cat}</option>)}
              </select>
              <ul id="search">
                {group.map(opt => {
                  const index = addArrays.group.indexOf(opt.toLowerCase())
                  if(index >= 0) {
                    return <li key={opt}><label><input type="checkbox" name={opt} onChange={handleGroup} value={opt.toLowerCase()} checked />{opt}</label></li>
                  }

                  return <li key={opt}><label><input type="checkbox" name={opt} onChange={handleGroup} value={opt.toLowerCase()} />{opt}</label></li>
                })}
              </ul>
            {recipes && recipes.length > 0 ? recipes.map((recipe, i) => <Recipe key={i} recipe={recipe} />) : <p>Ei vielä omia reseptejä</p>}
          </div>
        </div>
      </div>
    </div>
  );
}

export default MyRecipes
